<template>
  <div id="home">
    <navbar title="jxm"></navbar>
    <mt-swipe :auto="4000" id="banner">
      <mt-swipe-item class="banner-slide" v-for="item in bannerData">
        <a v-bind:href="item.jumpURL">
          <img v-lazy="item.picURL" class="banner-slide-image">
        </a>
      </mt-swipe-item>
    </mt-swipe>

    <div @click="go">gogogo</div>
    <div>{{mobile}}</div>

  </div>
</template>


<script>
  import detail from '../productDetail/productDetail'
  import bus from '../../../assets/js/eventBus'
  export default {
    name: 'hoome',
    props: ['code'],
    data () {
      return {
        bannerData: [{picURL: 'http://tcjxm6.xyz/res/banner.jpg', jumpURL: 'http://www.baidu.com'},
                     {picURL: 'http://tcjxm6.xyz/res/banner.jpg', jumpURL: 'http://www.baidu.com'},
                     {picURL: 'http://tcjxm6.xyz/res/banner.jpg', jumpURL: 'http://www.baidu.com'},
                     {picURL: 'http://tcjxm6.xyz/res/banner.jpg', jumpURL: 'http://www.baidu.com'}],
        msg: 'null'
      }
    },
    components: {
      detail
    },
    methods: {
      go (event) {
        this.$router.push({path: '/productdetail', query: { name: 'jxm' }, params: {id: 'tttttt'}})
      }
    },
    created () {
      // 根据key名获取传递回来的参数，data就是map
      console.log('created')
      this.msg = 'change'
      bus.$on('sendMsg', function (data) {
        console.log('传值过来啦啦啦啦：' + data)
        this.msg = data
      })
    },
    computed: {
      mobile: function () {
        return this.$store.state.login.mobile
      }
    },
    mounted () {
//      let code = this.$utils.getUrlKey('code')
    }
  }
</script>

<style>
  #banner {
    height: 5rem;
    background-color: orange;
  }
  .banner-slide-image {

  }

</style>



