import Vue from 'vue'
import Router from 'vue-router'
import Vuex from 'vuex'
import Root from '../components/common/root/root'
import Home from '../components/home/home/home'
import ShoppingCart from '../components/shoppingCart/shoppingCart/shoppingCart'
import Mine from '../components/mine/mine/mine'
import ProductDetail from '../components/home/productDetail/productDetail'
// import login from '../login'
Vue.use(Router)
Vue.use(Vuex)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Root',
      component: Root,
      children: [
        {
          path: '/?:code',
          component: Home
        },
        {
          path: '/ShoppingCart',
          component: ShoppingCart
        },
        {
          path: '/Mine',
          component: Mine
        },
        {
          path: '/Home',
          component: Home
        }
      ]
    },
    {
      path: '/productdetail',
      name: 'ProductDetail',
      component: ProductDetail
    }

  ]
})



